import React, { useState, useEffect } from "react"
import confetti from "https://esm.run/canvas-confetti@1"
import SurpriseButton from "./components/SurpriseButton"
import "./App.css"

const Confetti = () => {
  confetti({
    particleCount: 150,
    spread: 60
  })
}

const BirthdayTimer = () => {
  const calculateTimeLeft = () => {
    // Get the current time in Spain time zone
    const now = new Date()
    const spainTime = new Date(now.toLocaleString("en-US", { timeZone: "Europe/Madrid" }))

    // Set the target time to May 6 at midnight in Spain time zone
    const targetTime = new Date(spainTime.getFullYear(), 4, 6, 0, 0, 0) // May is 4 (0-based index), 6 is the day

    // Calculate the time difference
    const difference = targetTime - spainTime
    let timeLeft = {}

    if (difference > 0) {
      timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60)
      }
    }

    return timeLeft
  }

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft())

  useEffect(() => {
    const timer = setTimeout(() => {
      setTimeLeft(calculateTimeLeft())
    }, 1000)

    return () => clearTimeout(timer)
  })

  useEffect(() => {
    if (timeLeft.days === 0 && timeLeft.hours === 0 && timeLeft.minutes === 0 && timeLeft.seconds === 0) {
      Confetti() // Trigger confetti when timer reaches 0
    }
  }, [timeLeft])

  const timerComponents = []

  Object.keys(timeLeft).forEach((interval) => {
    if (!timeLeft[interval] && interval !== "seconds") {
      // Check if interval is seconds
      return
    }

    timerComponents.push(
      <div className="timer-container" key={interval}>
        <span className="timer-number">
          {interval === "seconds" ? String(timeLeft[interval]).padStart(2, "0") : timeLeft[interval]}
        </span>{" "}
        <span className="timename">{interval} </span>
      </div>
    )
  })

  return (
    <div>
      {timerComponents.length ? (
        <div className="timer-div">
          <h1 className="timer-heading">Darla ko birthday</h1>
          <h2>{timerComponents}</h2>
          <div className="button-container">
            <img src={require("./assets/img/dumpling-hands.png")} className="dumpling-hands" alt="Dumpling's hands" />
            <SurpriseButton />
            <img src={require("./assets/img/dumpling.png")} className="dumpling-img" alt="dumpling" />
          </div>
        </div>
      ) : (
        /************************************************
         *Esma Birthday wana component rakhne ↓↓↓ *******
         ************************************************/
         <div>Happy birthday</div>
      )}
    </div>
  )
}

const App = () => {
  return (
    <>
      <div className="main-container">
        <BirthdayTimer />
      </div>
    </>
  )
}

export default App
